export default {
	data() {
		return {
			showAgree: false,
			code: '',
			mobile: '',
			password: '',
			sending: false,
			sendTime: '获取验证码',
			count: 60,
			invitation: '',
			invitations: '',
		}
	},
	methods: {
		isShowAgree() {
			//是否选择协议
			this.showAgree = !this.showAgree;
		},
		showMa() {
			this.invitation = this.$queue.getInvitation()
		},
		sendMsg() {
			const {
				mobile
			} = this;
			if (!mobile) {
				this.$queue.showToast("请输入手机号");
			} else if (mobile.length !== 11) {
				this.$queue.showToast("请输入正确的手机号");
			} else {
				this.$queue.showLoading("正在发送验证码...");
				this.$Request.getT("/user/sendMsg/" + mobile + "/register").then(res => {
					if (res.status === 0) {
						this.sending = true;
						this.$queue.showToast('验证码发送成功请注意查收');
						this.countDown();
						uni.hideLoading();
					} else {
						uni.hideLoading();
						uni.showModal({
							showCancel: false,
							title: '短信发送失败',
							content: res.msg ? res.msg : '请一分钟后再获取验证码',
						});
					}
				});
			}
		},
		countDown() {
			const {
				count
			} = this;
			if (count === 1) {
				this.count = 60;
				this.sending = false;
				this.sendTime = '获取验证码'
			} else {
				this.count = count - 1;
				this.sending = true;
				this.sendTime = count - 1 + '秒后重新获取';
				setTimeout(this.countDown.bind(this), 1000);
			}
		},
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},


		navTo(url) {
			uni.navigateTo({
				url
			})
		},
		toLogin() {
			const {
				mobile,
				password,
				code,
				showAgree,
				invitation
			} = this;
			if (!mobile) {
				this.$queue.showToast("请输入账号");
			} else if (!password) {
				this.$queue.showToast("请设置密码");
			} else if (password.length < 6) {
				this.$queue.showToast("密码位数必须大于六位");
			} else if (invitation.length == 0) {
				this.$queue.showToast("请输入邀请码");
			} else if (!showAgree) {
				this.$queue.showToast("请先同意《协议》");
			} else {
				this.logining = true;
				this.$queue.showLoading("注册中...");
				this.$Request.postJson("/user/bindOpenid", {
					pwd: password,
					phone: mobile,
					msg: code,
					platform: "app",
					invitation: invitation,
					openid: this.$queue.getData("openid")
				}).then(res => {
					if (res.status === 0) {
						this.$queue.setData("token", res.data.uuid);
						this.$queue.setData("userId", res.data.userId);
						this.$queue.setData("mobile", mobile);
						this.getUserInfo(res.data.userId, res.data.uuid);
					} else {
						uni.hideLoading();
						uni.showModal({
							showCancel: false,
							title: '注册失败',
							content: res.msg,
						});
					}
				});
			}
		},
		getUserInfo(userId, token) {
			this.$Request.getT("/user/" + userId).then(res => {
				if (res.status === 0) {
					this.$queue.setData("image_url", res.data.image_url ? res.data.image_url : '/static/img/common/logo.jpg');
					this.$queue.setData("relation_id", res.data.relationId);
					this.$queue.setData("relation", res.data.invitation);
					this.$queue.setData("grade", res.data.grade);
					this.$queue.setData("isInvitation", res.data.isInvitation);
					this.$queue.setData("nickName", res.data.nickName ? res.data.nickName : res.data.phone);
					this.$queue.setData("gender", parseInt(res.data.gender));
					let href = this.$queue.getData("href");
					if (href) {
						if (href === '/pages/member/publisher') {
							this.$queue.remove("href");
							uni.redirectTo({
								url: '/pages/member/publisher?uid=' + userId + '&token=' + token
							});
						} else if (href === '/pages/member/user') {
							this.$queue.remove("href");
							uni.switchTab({
								url: '/pages/member/user'
							});
						} else {
							this.$queue.remove("href");
							uni.redirectTo({
								url: href
							});
						}
					} else {
						uni.switchTab({
							url: '/pages/index/index'
						});
					}
				} else {
					uni.showModal({
						showCancel: false,
						title: '登录失败',
						content: res.msg,
					});
					this.$queue.logout();
				}
				uni.hideLoading();
			});
		}
	},

}
